import Exa from 'exa-js';
import { Recruiters, Recruiter } from './types';
import { log } from './utils';

/**
 * Fast recruiter finder using Exa AI Search API
 * Searches LinkedIn and company pages for recruiters
 */
export class ExaRecruiterFinder {
  private exa: Exa;

  constructor() {
    const apiKey = process.env.EXA_API_KEY;
    if (!apiKey) {
      throw new Error('EXA_API_KEY is required for fast recruiter search');
    }
    this.exa = new Exa(apiKey);
  }

  /**
   * Find recruiters at a company using Exa
   * Much faster than browser automation
   */
  async findRecruiters(companyName: string): Promise<Recruiters> {
    log(`[Exa] Finding recruiters at: ${companyName}`, 'info');
    const startTime = Date.now();

    const recruiters: Recruiter[] = [];
    const seenNames = new Set<string>();

    // Run searches in parallel
    const [linkedinResults, careersResults, teamResults] = await Promise.all([
      this.searchLinkedIn(companyName),
      this.searchCareersPage(companyName),
      this.searchTeamPage(companyName),
    ]);

    // Combine results, avoiding duplicates
    for (const recruiter of [...linkedinResults, ...careersResults, ...teamResults]) {
      const nameKey = recruiter.name.toLowerCase();
      if (!seenNames.has(nameKey)) {
        seenNames.add(nameKey);
        recruiters.push(recruiter);
      }
    }

    const duration = ((Date.now() - startTime) / 1000).toFixed(2);
    log(`[Exa] Found ${recruiters.length} recruiters in ${duration}s`, 'success');

    return recruiters.slice(0, 10);
  }

  private async searchLinkedIn(companyName: string): Promise<Recruiter[]> {
    log('[Exa] Searching LinkedIn...', 'info');

    try {
      // Search for recruiters on LinkedIn
      const result = await this.exa.searchAndContents(
        `site:linkedin.com/in "${companyName}" recruiter OR "talent acquisition" OR "HR" OR "hiring"`,
        {
          type: 'auto',
          numResults: 10,
          category: 'linkedin profile',
          text: { maxCharacters: 500 },
          livecrawl: 'preferred',
        }
      );

      const recruiters: Recruiter[] = [];

      for (const item of result.results) {
        // Extract name from LinkedIn URL or title
        let name = '';
        let title = '';

        // Parse LinkedIn title format: "Name - Title - Company | LinkedIn"
        if (item.title) {
          const parts = item.title.split(/[-–|]/);
          name = parts[0]?.trim() || '';
          title = parts[1]?.trim() || '';
        }

        // Validate this is actually a recruiter
        const text = (item.text || '').toLowerCase();
        const isRecruiter =
          text.includes('recruit') ||
          text.includes('talent') ||
          text.includes('hiring') ||
          text.includes('hr ') ||
          text.includes('human resources') ||
          text.includes('people operations');

        if (name && name.length > 2 && name.length < 50 && isRecruiter) {
          recruiters.push({
            name,
            title: title || 'Recruiter',
            linkedInUrl: item.url,
            department: this.extractDepartment(text),
          });
        }
      }

      log(`[Exa] Found ${recruiters.length} recruiters on LinkedIn`, 'success');
      return recruiters;
    } catch (error) {
      log(`[Exa] LinkedIn search failed: ${error}`, 'warn');
      return [];
    }
  }

  private async searchCareersPage(companyName: string): Promise<Recruiter[]> {
    log('[Exa] Searching careers page...', 'info');

    try {
      const result = await this.exa.searchAndContents(
        `${companyName} careers recruiting team contact hiring`,
        {
          type: 'auto',
          numResults: 5,
          text: { maxCharacters: 1500 },
          summary: true,
        }
      );

      const recruiters: Recruiter[] = [];
      const namePattern = /([A-Z][a-z]+ [A-Z][a-z]+)(?:,?\s*(?:is|serves as|works as|\-)\s*)?(?:our\s+)?(?:Head of|Director of|VP of|Manager of)?\s*(Recruit|Talent|HR|People|Hiring)/gi;

      for (const item of result.results) {
        const text = item.text || item.summary || '';
        const matches = text.matchAll(namePattern);

        for (const match of matches) {
          const name = match[1];
          if (name && name.length > 4 && name.length < 40) {
            recruiters.push({
              name,
              title: `${match[2]} Team`,
              linkedInUrl: undefined,
              department: undefined,
            });
          }
        }
      }

      log(`[Exa] Found ${recruiters.length} recruiters on careers pages`, 'success');
      return recruiters;
    } catch (error) {
      log(`[Exa] Careers page search failed: ${error}`, 'warn');
      return [];
    }
  }

  private async searchTeamPage(companyName: string): Promise<Recruiter[]> {
    log('[Exa] Searching team/about pages...', 'info');

    try {
      const result = await this.exa.searchAndContents(
        `${companyName} "our team" OR "about us" "people" OR "HR" OR "recruiting"`,
        {
          type: 'auto',
          numResults: 5,
          text: { maxCharacters: 1500 },
        }
      );

      const recruiters: Recruiter[] = [];

      // Look for common HR/recruiting title patterns
      const titlePatterns = [
        /([A-Z][a-z]+ [A-Z][a-z]+)\s*[-–:]\s*(Head of People|VP People|Chief People Officer|Director of Talent|Recruiting Manager|HR Director|Talent Lead)/gi,
        /(Head of People|VP People|Chief People Officer|Director of Talent|Recruiting Manager|HR Director)\s*[-–:]\s*([A-Z][a-z]+ [A-Z][a-z]+)/gi,
      ];

      for (const item of result.results) {
        const text = item.text || '';

        for (const pattern of titlePatterns) {
          const matches = text.matchAll(pattern);
          for (const match of matches) {
            const name = match[1].includes('of') ? match[2] : match[1];
            const title = match[1].includes('of') ? match[1] : match[2];

            if (name && name.length > 4 && name.length < 40) {
              recruiters.push({
                name,
                title,
                linkedInUrl: undefined,
                department: 'People',
              });
            }
          }
        }
      }

      log(`[Exa] Found ${recruiters.length} recruiters on team pages`, 'success');
      return recruiters;
    } catch (error) {
      log(`[Exa] Team page search failed: ${error}`, 'warn');
      return [];
    }
  }

  private extractDepartment(text: string): string | undefined {
    const deptKeywords = [
      { keyword: 'engineer', dept: 'Engineering' },
      { keyword: 'technical', dept: 'Engineering' },
      { keyword: 'product', dept: 'Product' },
      { keyword: 'design', dept: 'Design' },
      { keyword: 'sales', dept: 'Sales' },
      { keyword: 'marketing', dept: 'Marketing' },
      { keyword: 'university', dept: 'University' },
      { keyword: 'campus', dept: 'University' },
      { keyword: 'executive', dept: 'Executive' },
    ];

    for (const { keyword, dept } of deptKeywords) {
      if (text.includes(keyword)) {
        return dept;
      }
    }

    return undefined;
  }
}

export function isExaAvailable(): boolean {
  return !!process.env.EXA_API_KEY;
}
